html,
body {
  height: 100%;
}
body {
  min-width: 1024px;
  max-width: 1920px;
  margin: 0 auto;
}
header {
  box-sizing: border-box;
  background: none;
  padding: 0px;
  height: 90px;
}
#structure {
  position: relative;
  background: none;
  margin: 0 auto;
  min-height: 100%;
}
#content {
  background: none;
  padding-bottom: 120px;
}
#content .wrapper:before,
#content .wrapper:after {
  display: table;
  content: "";
}
#content .wrapper:after {
  clear: both;
}
aside,
main {
  box-sizing: border-box;
  float: left;
}
aside#left {
  background: none;
  width: 0px;
  padding: 0px;
}
aside#right {
  background: none;
  width: 0px;
  padding: 0px;
}
main {
  background: none;
  width: calc(100% - 0px - 0px);
  padding: 0px;
}
main.no-aside-left {
  width: calc(100% - 0px);
  padding-left: 0;
}
main.no-aside-right {
  width: calc(100% - 0px);
  padding-right: 0;
}
main.full,
main.no-aside-left.no-aside-right {
  float: none;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}
footer {
  position: relative;
  box-sizing: border-box;
  background: #FFFFFF;
  padding: 0px;
  text-align: left;
  margin: 0 auto;
}
footer > .wrapper {
  height: 90px;
  margin-top: -90px;
}
.wrapper {
  box-sizing: border-box;
  min-width: 1024px;
  max-width: 1024px;
  padding: 0 15px;
  margin: 0 auto;
}
.list,
.list-hr {
  margin: 0px;
  font: 12px/120% Tahoma, Verdana, Arial, Helvetica, sans-serif;
}
