.list-hr > li:before,
.list > li:after,
.list-hr > li:after {
  display: table;
  content: "";
}
.list > li:after,
.list-hr > li:after {
  clear: both;
}
.list > li.list-element-last,
.list-hr > li.list-element-last {
  padding-bottom: 0px;
  margin-bottom: 0px;
}
.list > li p:last-child,
.list-hr > li p:last-child {
