.popup {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1002;
  display: none;
  box-sizing: border-box;
  background: #FFFFFF;
  border: 0;
  box-shadow: none;
  border-radius: 0px;
  width: 300px;
  height: auto;
  text-align: left;
  white-space: normal;
}
.popup .popup-header,
.popup .popup-body,
.popup .popup-footer {
  box-sizing: border-box;
  width: 100%;
}
.popup .popup-header {
  background: none;
  border: 0;
  border-radius: 0px;
  height: 60px;
  padding: 20px 0px;
  font: 12px/120% Tahoma, Verdana, Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 120%;
  color: #000000;
  text-align: center;
}
.popup .popup-body {
  background: none;
  border: 0;
  border-radius: 0px;
  height: auto;
  padding: 0px;
  font: 12px/120% Tahoma, Verdana, Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 120%;
  color: #000000;
  text-align: left;
}
.popup .popup-footer {
  background: none;
  border: 0;
  border-radius: 0px;
  height: 60px;
  padding: 20px 0px;
  font: 12px/120% Tahoma, Verdana, Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 120%;
  color: #000000;
  text-align: center;
}
.popup .popup-close {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  display: inline-block;
  background: url(../images/button-close.png) no-repeat 0 0;
  width: 35px;
  height: 35px;
  border: 0;
  padding: 0px;
  font: 35px/100% Tahoma, Verdana, Arial, Helvetica, sans-serif;
  font-size: 35px;
  line-height: 100%;
  color: #02A3AA;
  text-decoration: none;
  vertical-align: top;
  cursor: pointer;
